import { ELEMENTS_TYPE } from '../Constants';
import { SURVEYS_ERROR_MESSAGE } from '../Messages';
import _get from 'lodash/get';
import { isValidPhoneNumber } from 'react-phone-number-input';
import { convertPhoneValue } from '../../../helpers/number';

export const onValidateHandler = (survey, options) => {
  const question = _get(options, 'question');
  if (question.getType() === ELEMENTS_TYPE.phoneInput) {
    const isRequired = _get(question, 'isRequired', false);
    phoneNumberValidator(options, isRequired);
  }
};

const phoneNumberValidator = (options, isRequired) => {
  const phoneNumberValue = _get(options, 'value', '');

  if (!phoneNumberValue && !isRequired) return;

  if (!isValidPhoneNumber(convertPhoneValue(phoneNumberValue)))
    options.error = SURVEYS_ERROR_MESSAGE.invalidPhoneNumber;
};
